html, body {
  margin: 0;
  background: transparent;
  min-height: 0;
}

span.cm-underlined {
  text-decoration: underline;
}
span.cm-strikethrough {
  text-decoration: line-through;
}
span.cm-brace {
  color: #170;
  font-weight: bold;
}
span.cm-table {
  color: blue;
  font-weight: bold;
}

.cm-tw-syntaxerror {
	color: #FFF;
	background-color: #900;
}

.cm-tw-deleted {
	text-decoration: line-through;
}

.cm-tw-header5 {
	font-weight: bold;
}
.cm-tw-listitem:first-child { /*Added first child to fix duplicate padding when highlighting*/
	padding-left: 10px;
}

.cm-tw-box {
	border-top-width: 0px !important;
	border-style: solid;
	border-width: 1px;
	border-color: inherit;
}

.cm-tw-underline {
	text-decoration: underline;
}
@media only screen and (max-width: 2500px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 14px;
  }
}

@media only screen and (max-width: 600px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 13px;
  }
}

@media only screen and (max-width: 500px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 13px;
  }
}

@media only screen and (max-width: 400px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 12px;
  }
}


@media only screen and (max-width: 320px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 10px;
  }
}

.videoWrapper {
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	padding-top: 25px;
	height: 0;
	overflow: hidden;
  margin-bottom: 20px;
}
.videoWrapper iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 90%;
	height: 90%;
}

@media only screen and (min-width: 600px) {
	.paddedText {
		margin-left: 16px;
	}
}

@media only screen and (max-width: 600px) {
	.paddedText {
		margin-left: 0px;
	}
}

body {
  font-family: 'Arial', sans-serif;
  background-color: white;
}

.Demo__some-network {
  vertical-align: top;
  display: inline-block;
  margin-right: 15px;
  text-align: center;
}

.Demo__some-network__share-count {
  margin-top: 3px;
  font-size: 12px;
}

.Demo__some-network__share-button {
  cursor: pointer;
}

.Demo__some-network__share-button:hover:not(:active) {
  opacity: 0.75;
}

.Demo__some-network__custom-icon {
  width:32px;
  height:32px;
}

